  }   
  .football-hotnews-center{
    padding-left: 10px;
  }
  .football-hotnews-img{
    margin-left:10px;
    height:120px;
    width: 100%;
	overflow: hidden;
  }
   @media only screen and (max-width:990px) {
     .football-hotnews-img{
       height: 90px;
     }
  } 
  .football-hotnews-img img{
    height: 100%;
    width: 100%;
  }
  .football-hotnews-img img:hover{
    transform: scale(1.1);
    transition: all 0.5s linear;
  }
  .football-hotnews-title{
    text-align: left;
	padding-left: 10px;
    height:3.2rem;
    line-height: 1.5625rem;
	overflow:hidden;
	text-overflow:ellipsis;
	display:-webkit-box;
	-webkit-box-orient:vertical;
	-webkit-line-clamp:2;
   }
  @media only screen and (max-width:990px) {
    .football-hotnews-title{
      height: 50px;
    }
 }
  .football-hotnews-time{
    height: 30px;
    text-align: left;
   }
  .football-hotnews-writer{
	font-size: 0.9375rem;
	color: #999;	  
    padding-right: 20px;
    float: right;
  }

  .football-hotnews-item:hover{
    box-shadow: 0 0 1px 2px rgba(225, 69, 0, .1);
  }

</style>



