<template>
   <div class="contact">	
	 <div class="contact-title">
			 CONTACT
	 </div>	 
	 <el-row type="flex" >
		 <el-col :span="1"></el-col>
		 <el-col :span="22" >
			 <el-col :span="24" :xs="24" :sm="24" :md="24" :lg="24">
				 <div class="contact-content-center">
					<p></p>
					
					<p></p>
					
					<p></p>
					
					<p></p>
					
					<p></p>
				 </div>				 
			 </el-col>
			 <el-col :span="8" :xs="24" :sm="24" :md="8" :lg="8">
				 <div class="contact-content-left">
					 <p></p>
					 <p>2067707</p>
					 <p></p>
					 <p>0531-88806969/13370581312</p>
					 <p>0531-88806969</p>
					 <p>hm@stnn.cc</p>
				 </div>				 
			 </el-col>
			 <el-col :span="16" :xs="24" :sm="24" :md="16" :lg="16">
				 <div class="contact-content-right">
				  <baidu-map class="ditu" :center="{lng:114.033880, lat:22.5378}" :zoom="20"><!--  -->
					<bm-marker :position="markerPoint" :dragging="true" @click="infoWindowOpen">
					   <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">2067707</bm-info-window><!--  --> 
					</bm-marker>
				  </baidu-map>
				 </div>	 				 		 
			 </el-col>
		 </el-col>
		 <el-col :span="1"></el-col>
	 </el-row>



		
	</div>
</template>

<script>
export default {
  data () {
    return {
      show: true,
			markerPoint:{lng:114.033900, lat:22.537850}
    }
  },
  methods: {
    infoWindowClose () {
      this.show = false
    },
    infoWindowOpen () {
      this.show = true
    }
  }
}
</script>

<style scoped="scoped">
  a{
    color: #000000;
  }
  a:hover{
     color: orangered;
  }
  .contact{
    height: auto;
    margin: 0 auto;
    max-width: 1400px;
    margin-bottom: 50px;
	padding-bottom: 20px;
    background-color: white;
  }
  .contact-title{
    width: 100%;
    height: 60px;
    line-height: 60px;
    color:orangered;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    background-color: white;
    border-bottom:1px solid #FF6633;
  }
  
  .contact-content-center{
	height: auto;
	text-align: left;
	font-size: 1.3rem;
  }
  .contact-content-center p{
	height: auto;
	line-height: 40px;
	padding-bottom: 20px;
	text-indent: 2em;
	text-align: left;
  }
  .contact-content-left  {
	width: 100%;
	padding: 20px;
	height: auto;
	margin: 0 auto;
	text-align: left;
	font-size: 1.3rem;
	border: 1px solid #FF8C00;
	box-shadow: 10px 10px 10px 2px rgba(0, 0, 0, .5);
	/* background-color: red; */
  }
  .contact-content-left p {
	  
  }  
  
  .ditu{
	margin-left:10px;
    width:100%;
    height:400px;
    border:#ccc solid 1px;
  }
 @media only screen and (max-width:990px) {
      .ditu{
        margin-left:0px;
     }
   }
</style>


